import React, {useEffect, useState} from 'react';
import itemService from "../../service/item/ItemService";

const ItemComponent = (itemId) => {

    const [item, setItem] = useState({});

    useEffect( () => {
        itemService.getItem(itemId)
            .then((res) => {
                setItem(res.data)
            })
            .catch(error => console.log(error));
    }, []);

    return(
        <div>
            <div>id : {item.id}</div><br></br>
            <div>item_name : {item.itemName} </div><br></br>
            <div>item_category : {item.category} </div><br></br>
            <div>item_description : {item.description} </div><br></br>
            <div>item_memberName : {item.memberName} </div><br></br>
            <div>item_price : {item.price}원 </div><br></br>
            <div>item_createdTime : {item.createdTime} </div><br></br>
            <div>item_updatedTime : {item.updatedTime} </div><br></br>
        </div>
    );
}

export default ItemComponent;